import React, { Component } from 'react';
import { connect } from 'react-redux';
import ClassNames from 'classnames';

import actions from '../../redux/actions'

import styles from './FlexibleInfoBox.scss';
import { EXECUTION_PHASES } from '../../redux/reducers/playground.reducer';

class FlexibleInfoBox extends Component {
    constructor(props) {
        super(props);

        this.getImageClassName = this.getImageClassName.bind(this);
        this.getMsgTitle = this.getMsgTitle.bind(this);
        this.getMsgBody = this.getMsgBody.bind(this);
    }
    getImageClassName() {
        const { currentPhase } = this.props;
    
        switch(currentPhase) {
            case EXECUTION_PHASES.COMPILE:
            case EXECUTION_PHASES.COMPILE_FINISH:
                return "FlexibleInfoBox-compile-image"
            case EXECUTION_PHASES.DEPLOY:
            case EXECUTION_PHASES.DEPLOY_FINISH:
                return "FlexibleInfoBox-deploy-image"
            case EXECUTION_PHASES.RUN:
            case EXECUTION_PHASES.RUN_FINISH:
                return "FlexibleInfoBox-run-image"
            default:
                return "FlexibleInfoBox-compile-image"
        }
    }
    getMsgTitle() {
        const { currentPhase } = this.props;
    
        switch(currentPhase) {
            case EXECUTION_PHASES.COMPILE:
            case EXECUTION_PHASES.COMPILE_FINISH:
                return "Compile Success"
            case EXECUTION_PHASES.DEPLOY:
            case EXECUTION_PHASES.DEPLOY_FINISH:
                return "Deploy Success"
            case EXECUTION_PHASES.RUN:
            case EXECUTION_PHASES.RUN_FINISH:
                return "Run Success"
            default:
                return ""
        }
    }
    getMsgBody() {
        const { currentPhase } = this.props;
        
        switch(currentPhase) {
            case EXECUTION_PHASES.COMPILE:
            case EXECUTION_PHASES.COMPILE_FINISH:
                return "Your contract has compiled. Check the ABI, Bytecode, and ASM below."
            case EXECUTION_PHASES.DEPLOY:
            case EXECUTION_PHASES.DEPLOY_FINISH:
                return "Your bytecode has deployed to blockchain. Check the address of your bytecode."
            case EXECUTION_PHASES.RUN:
            case EXECUTION_PHASES.RUN_FINISH:
                return "Your bytecode has run on the blockchain. Check the output below"
            default:
                return ""
        }        
    }
    render() {
        const { isMsgBoxShow, isMsgBoxHide, hideMsgBox } = this.props;
        console.log(hideMsgBox);

        return (
            <div
                className="FlexibleInfoBox"
            >
                <div
                    className="FlexibleInfoBox-image-wrapper"
                    style={{
                        animationName: 'hide-execution-result',
                    }}
                >
                    <div
                        className={this.getImageClassName()}
                    >
                    </div>
                </div>
                <div
                    className={ClassNames({
                        "FlexibleInfoBox-message-box": true,
                        "FlexibleInfoBox-message-box-show": isMsgBoxShow? true: false,
                        "FlexibleInfoBox-message-box-hide": isMsgBoxHide? true: false,
                    })}
                    style={{
                        display: isMsgBoxShow? 'block': 'none',
                    }}
                >
                    <div
                        className="FlexibleInfoBox-message-box-title"
                    >
                        <div
                            className="FlexibleInfoBox-message-box-title-ic"
                        >
                        
                        </div>
                        <div
                            className="FlexibleInfoBox-message-box-title-text"
                        >
                            {this.getMsgTitle()}
                        </div>                        
                    </div>
                    <div
                        className="FlexibleInfoBox-message-box-body"
                    >
                        <span
                            className="FlexibleInfoBox-message-box-body-text"
                        >
                            {this.getMsgBody()}
                        </span>
                        <div
                            className="FlexibleInfoBox-message-box-body-close-btn"
                            onClick={hideMsgBox}
                        > 
                        </div>                                        
                    </div>                                   
                </div>             
            </div>
        )
    }
}

const mapStateToProps = state => {
    const { currentPhase, isMsgBoxShow, isMsgBoxHide } = state.playgroundReducer;

    return {
        currentPhase,
        isMsgBoxShow,
        isMsgBoxHide,
    }
}

const mapDispatchToProps = {
    hideMsgBox: actions.playgroundActions.hideMsgBox,
}

export default connect(mapStateToProps, mapDispatchToProps)(FlexibleInfoBox);